import axios from "axios";

export default class XGithub {
    options = {
        el: '.github-page',
        loadingEl: '.loading-nav',
        errorEl: '.error-nav',
        user: 'benzBrake',
        colors: ["dark", "light", "light", "light", "light", "white", "light", "white"],
        bgColors: ["light", "info", "dark", "success", "secondary", "warning", "primary", "danger"],
        btnColors: ["dark", "light", "primary", "light", "info", "light", "light", "light"],
        visitText: 'Check',
        template: '<div class="p-3 {BG_COLOR} rounded"><div class="github-language {TEXT_COLOR}">{PROJECT_LANGUAGE}</div>' +
            '<div class="d-flex flex-column">' +
            '<span class="py-1 text-ellipsis font-thin h3 {TEXT_COLOR}">{REPO_NAME}</span>' +
            '<small class="py-2 text-sm {TEXT_COLOR}"><i class="iconfont icon-star m-r-xs"></i>{REPO_STARS} stars / <i class="iconfont icon-fork"></i> {REPO_FORKS} forks</small>' +
            '<small class="my-2 text-ellipsis desc {TEXT_COLOR}">{REPO_DESC}</small>' +
            '<a target="_blank" href="{REPO_URL}" class="py-1 d-block text-sm btn btn-rounded btn-sm lter {BUTTON_COLOR}">{VISIT_TEXT}</a>' +
            '          </div>\n' +
            '        </div>'
    };

    constructor(options = {}) {
        Object.assign(this.options, options);
        this.init();
    }

    init() {
        this.container = document.querySelector(this.options.el);
        this.loadingDom = this.container.querySelector(this.options.loadingEl);
        this.errorDom = this.container.querySelector(this.options.errorEl);
        let _t = this;
        axios.get(`https://api.github.com/users/${_t.options.user}/repos`)
            .then(r => {
                _t.loadingDom.classList.add('d-none');
                let contentWrapper = _t.container.querySelector('.github-contain');
                contentWrapper.classList.remove('d-none');
                for (let i in r.data) {
                    let repo = r.data[i];
                    repo.updated_at = repo.updated_at.substring(0, repo.updated_at.lastIndexOf("T"));
                    if (repo.language == null) {
                        repo.language = "";
                    }

                    if (repo.description == null) {
                        repo.description = "";
                    }

                    let item = _t.options.template.replace("{REPO_NAME}", repo.name)
                        .replace("{REPO_URL}", repo.html_url)
                        .replace("{REPO_STARS}", repo.stargazers_count)
                        .replace("{REPO_FORKS}", repo.forks_count)
                        .replace("{REPO_DESC}", repo.description)
                        .replace("{BG_COLOR}", "bg-" + _t.options.bgColors[i % 8])
                        .replace("{BUTTON_COLOR}", "btn-" + _t.options.btnColors[(i) % 8])
                        .replace(/{TEXT_COLOR}/g, "text-" + _t.options.colors[(i) % 8])
                        .replace("{VISIT_TEXT}", _t.options.visitText)
                        .replace("{PROJECT_LANGUAGE}", repo.language);
                    let div = document.createElement('div');
                    div.setAttribute('class', 'col-12 col-md-6 p-2');
                    div.innerHTML = item;
                    contentWrapper.appendChild(div);
                }
            });
    }
}
document.addEventListener('DOMContentLoaded', () => {
    if (window.github_user.length == 0) {
        new XGithub();
    } else {
        new XGithub({user: window.github_user});
    }

})
